<template>
	<view class="certificate-page">
		<!-- È°µÈù¢ËøîÂõûÂ§¥ÈÉ®ÁªÑ‰ª∂ -->
		<pageBack :titleInfo="titleInfo"></pageBack>

		<view class="content">
			<view class="container">
				<!-- Êú™ÁôªÂΩïÁä∂ÊÄÅ -->
				<view class="empty-state" v-if="!userInfo || !userInfo.id">
					<text class="empty-icon">üìú</text>
					<text class="empty-text">ËØ∑ÂÖàÁôªÂΩïÊü•ÁúãËØÅ‰π¶</text>
					<view class="login-btn" @click="toLogin">Á´ãÂç≥ÁôªÂΩï</view>
				</view>

				<!-- Â∑≤ÁôªÂΩï‰ΩÜÊú™Ëé∑ÂæóËØÅ‰π¶ -->
				<view class="empty-state" v-else-if="!hasCertificate">
					<text class="empty-icon">üèÜ</text>
					<text class="empty-text">ÊöÇÊú™Ëé∑ÂæóËØÅ‰π¶</text>
					<text class="empty-desc">Á¥ØËÆ°ÊçêÁåÆÈ¶ôÁÅ´ÂÄºËææÂà∞ 5000 ÁÇπÂç≥ÂèØËé∑ÂæóËØÅ‰π¶</text>
					<view class="progress-box">
						<view class="progress-label">
							<text>ÂΩìÂâçËøõÂ∫¶Ôºö{{ userInfo.balance || 0 }} / 5000</text>
							<text class="progress-percent">{{ progressPercent }}%</text>
						</view>
						<view class="progress-bar">
							<view class="progress-fill" :style="{ width: progressPercent + '%' }"></view>
						</view>
					</view>
