				</view>

				<!-- Â∑≤Ëé∑ÂæóËØÅ‰π¶ -->
				<view class="certificate-container" v-else>
					<!-- ËØÅ‰π¶Âç°Áâá - Â¢ûÂä†refÊ†áËØÜ -->
					<view class="certificate-card" ref="certificateRef" id="certificateDom">
						<!-- Â∑¶‰æßË£ÖÈ•∞Ëä±Á∫π -->
						<view class="decoration-left">
							<view class="decoration-line"></view>
							<view class="decoration-circle"></view>
							<view class="decoration-line"></view>
						</view>
						
						<!-- Âè≥‰æßË£ÖÈ•∞Ëä±Á∫π -->
						<view class="decoration-right">
							<view class="decoration-line"></view>
							<view class="decoration-circle"></view>
							<view class="decoration-line"></view>
						</view>
						
						<!-- ËØÅ‰π¶Ê†áÈ¢òÂå∫Âüü -->
						<view class="certificate-header">
							<view class="header-icon">‚ú®</view>
							<text class="certificate-title">ÂäüÂæ∑ËØÅ‰π¶</text>
							<text class="certificate-subtitle">Certificate of Merit</text>
							<view class="header-divider"></view>
						</view>
						
						<!-- ËØÅ‰π¶ÂÜÖÂÆπÂå∫Âüü -->
						<view class="certificate-body">
							<view class="certificate-content">
								<view class="name-wrapper">
									<text class="name-label">ÂäüÂæ∑‰∏ª</text>
									<text class="certificate-name">{{ userInfo.nickname || 'ÂñÑ‰ø°' }}</text>
								</view>
								
								<view class="merit-wrapper">
									<text class="merit-text">ËôîËØö‰æõÂ•âÔºåÂäüÂæ∑Êó†Èáè</text>
									<text class="merit-desc">ÂøÉËØöÂàôÁÅµÔºåÂñÑÂøµÊàêÁúü</text>
								</view>
								
								<view class="info-wrapper">
									<view class="info-item">
										<text class="info-label">Á¥ØËÆ°È¶ôÁÅ´ÂÄº</text>
										<text class="info-value">{{ totalIncense || userInfo.balance }} ÁÇπ</text>
									</view>
									<view class="info-item">
										<text class="info-label">Ëé∑ÂæóÊó•Êúü</text>
										<text class="info-value">{{ certificateDate }}</text>
									</view>
								</view>
							</view>
							
							<!-- Âç∞Á´† -->
							<view class="certificate-seal">
								<view class="seal-circle">
									<text class="seal-text">ÂäüÂæ∑</text>
									<text class="seal-text">ÂúÜÊª°</text>
								</view>
							</view>
						</view>
						
						<!-- ËØÅ‰π¶Â∫ïÈÉ® -->
						<view class="certificate-footer">
							<view class="footer-divider"></view>
							<text class="certificate-org">Â¶àÁ•ñÊñáÂåñ‰º†Êâø</text>
							<text class="certificate-org-en">Mazu Cultural Heritage</text>
						</view>
					</view>
					
					<!-- Êìç‰ΩúÊåâÈíÆ -->
					<view class="certificate-actions">
						<view class="action-btn share-btn" @click="shareCertificate">
							<text>ÂàÜ‰∫´ËØÅ‰π¶</text>
						</view>
						<view class="action-btn save-btn" @click="saveCertificate">
							<text>‰øùÂ≠òÂõæÁâá</text>
						</view>
					</view>
					
					<!-- ÈöêËóèÁöÑÁîªÂ∏É - Áî®‰∫éÁîüÊàêËØÅ‰π¶ÂõæÁâá -->
					<canvas 
						canvas-id="certificateCanvas" 
						ref="certCanvas"
						style="position: fixed; top: -9999rpx; left: -9999rpx; width: 680px; height: 800px;"
					></canvas>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	// ÂºïÂÖ•ËøîÂõûÂ§¥ÈÉ®ÁªÑ‰ª∂
	import pageBack from '/components/title/title.vue';
	import { useUserInfoStore } from "@/store/userInfo.js";

	export default {
		name: 'MyCertificatePage',
		components: {
			pageBack
		},
		data() {
			return {
				// Â§¥ÈÉ®Ê†áÈ¢òÈÖçÁΩÆ
				titleInfo: {
					titleShow: true,
					title: 'ÊàëÁöÑËØÅ‰π¶',
					imageUrl: 'http://t73sifiwt.hn-bkt.clouddn.com/pageBack/3.webp',
					heightShow: false,
					backShow: true
				},
				userInfo: {},
				totalIncense: 0, // ÊÄªÈ¶ôÁÅ´ÂÄºÔºàÁî®‰∫éËØÅ‰π¶ÊòæÁ§∫Ôºâ
				certificateDate: '', // Ëé∑ÂæóËØÅ‰π¶Êó•Êúü
				certificateImage: '' // ËØÅ‰π¶ÂõæÁâá‰∏¥Êó∂Ë∑ØÂæÑ
			};
		},
		computed: {
			// ÊòØÂê¶Â∑≤Ëé∑ÂæóËØÅ‰π¶ÔºàÈ¶ôÁÅ´ÂÄº >= 5000Ôºâ
			hasCertificate() {
				return this.userInfo && this.userInfo.balance >= 5000;
			},
			// ËøõÂ∫¶ÁôæÂàÜÊØî
			progressPercent() {
				if (!this.userInfo || !this.userInfo.balance) return 0;
				const percent = (this.userInfo.balance / 5000) * 100;
				return percent > 100 ? 100 : Math.round(percent);
			}
		},
		onLoad() {
			this.loadUserInfo();
		},
		onShow() {
			// ÊØèÊ¨°ÊòæÁ§∫È°µÈù¢Êó∂ÈáçÊñ∞Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÔºàÂèØËÉΩÈ¶ôÁÅ´ÂÄºÊúâÊõ¥Êñ∞Ôºâ
			this.loadUserInfo();
		},
		// Â∞èÁ®ãÂ∫èÂàÜ‰∫´Èí©Â≠ê
		onShareAppMessage() {
			return {
				title: `ÊàëÁöÑÂ¶àÁ•ñÂäüÂæ∑ËØÅ‰π¶`,
				path: '/pages/certificate/certificate',
				imageUrl: this.certificateImage || ''
			};
		},
		// ÊúãÂèãÂúàÂàÜ‰∫´Èí©Â≠ê
		onShareTimeline() {
			return {
				title: `ÊàëÁöÑÂ¶àÁ•ñÂäüÂæ∑ËØÅ‰π¶ÔºåÁ¥ØËÆ°ÊçêÁåÆ${this.userInfo.balance || 0}ÁÇπÈ¶ôÁÅ´ÂÄºÔºÅ`,
				imageUrl: this.certificateImage || ''
			};
		},
		methods: {
			/**
			 * Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
			 */
			loadUserInfo() {
				const store = useUserInfoStore();
				this.userInfo = store.$state.userInfo || {};
				
				// Â¶ÇÊûúÂ∑≤Ëé∑ÂæóËØÅ‰π¶ÔºåËÆ∞ÂΩïËØÅ‰π¶‰ø°ÊÅØ
				if (this.hasCertificate) {
					this.totalIncense = this.userInfo.balance || 0;
					// Ëé∑ÂèñËØÅ‰π¶Êó•ÊúüÔºàÂ¶ÇÊûúÂ∑≤‰øùÂ≠òÂàôËØªÂèñÔºåÂê¶Âàô‰ΩøÁî®ÂΩìÂâçÊó•ÊúüÔºâ
					const savedDate = uni.getStorageSync('certificate_date');
					if (savedDate) {
						this.certificateDate = savedDate;
					} else {
						// È¶ñÊ¨°ËææÂà∞5000Êó∂‰øùÂ≠òÊó•Êúü
						const now = new Date();
						this.certificateDate = `${now.getFullYear()}Âπ¥${now.getMonth() + 1}Êúà${now.getDate()}Êó•`;
						uni.setStorageSync('certificate_date', this.certificateDate);
					}
				}
			},
			/**
			 * ÂâçÂæÄÁôªÂΩï
			 */
			toLogin() {
				uni.navigateTo({
					url: "/pages/login/login"
				});
			},
			/**
			 * ÂâçÂæÄÊçêÁåÆÈ¶ôÁÅ´È°µÈù¢
			 */
			goToIncense() {
				uni.navigateTo({
					url: "/secondPages/incense/incense"
				});
			},
			/**
			 * Â∞èÁ®ãÂ∫è‰∏ìÁî®ÔºöÂ∞ÜËØÅ‰π¶ËäÇÁÇπÁîüÊàêÂõæÁâáÔºàÊ†∏ÂøÉ‰øÆÂ§çÔºâ
			 */
			async convertCertificateToImage() {
				return new Promise((resolve, reject) => {
					uni.showLoading({
						title: 'ÁîüÊàêËØÅ‰π¶‰∏≠...'
					});

					// 1. ‰ΩøÁî®Â∞èÁ®ãÂ∫èÈÄâÊã©Âô®Ëé∑ÂèñËØÅ‰π¶ËäÇÁÇπ‰ø°ÊÅØ
					const query = uni.createSelectorQuery().in(this);
					query.select('#certificateDom').boundingClientRect(rect => {
						if (!rect) {
							uni.hideLoading();
							uni.showToast({
								title: 'ËØÅ‰π¶ËäÇÁÇπÊú™ÊâæÂà∞',
								icon: 'none'
							});
							reject('ËØÅ‰π¶ËäÇÁÇπÊú™ÊâæÂà∞');
							return;
						}

						// 2. ‰ΩøÁî®Â∞èÁ®ãÂ∫èÂéüÁîüÁîªÂ∏ÉÁîüÊàêÂõæÁâáÔºàÂÖºÂÆπÊñπÊ°àÔºâ
						// ÊñπÊ°à1Ôºö‰ΩøÁî®wx.canvasToTempFilePathÔºàÊé®ËçêÔºâ
						uni.canvasToTempFilePath({
							x: 0,
							y: 0,
							width: rect.width,
							height: rect.height,
							destWidth: rect.width * 3, // 3ÂÄçÊ∏ÖÊô∞Â∫¶
							destHeight: rect.height * 3,
							canvasId: 'certificateCanvas',
							success: (res) => {
								// ÊñπÊ°à2ÔºöÂ¶ÇÊûúÁõ¥Êé•Êà™ÂõæÂ§±Ë¥•Ôºå‰ΩøÁî®ÊâãÂä®ÁªòÂà∂ÁîªÂ∏ÉÁöÑÂÖúÂ∫ïÊñπÊ°à
								if (!res.tempFilePath) {
									this.drawCertificateToCanvas(rect.width, rect.height).then(tempPath => {
										uni.hideLoading();
										this.certificateImage = tempPath;
										resolve(tempPath);
									}).catch(err => {
										uni.hideLoading();
										reject(err);
									});
									return;
								}
								
								uni.hideLoading();
								this.certificateImage = res.tempFilePath;
								resolve(res.tempFilePath);
							},
							fail: (err) => {
								// ÂÖúÂ∫ïÊñπÊ°àÔºöÊâãÂä®ÁªòÂà∂ËØÅ‰π¶
								this.drawCertificateToCanvas(rect.width, rect.height).then(tempPath => {
									uni.hideLoading();
									this.certificateImage = tempPath;
									resolve(tempPath);
								}).catch(err2 => {
									uni.hideLoading();
									uni.showToast({
										title: 'ÁîüÊàêËØÅ‰π¶Â§±Ë¥•',
										icon: 'none'
									});
									reject(err2);
								});
							}
						}, this);
					}).exec();
				});
			},
			/**
			 * ÂÖúÂ∫ïÊñπÊ°àÔºöÊâãÂä®ÁªòÂà∂ËØÅ‰π¶Âà∞ÁîªÂ∏É
			 */
			drawCertificateToCanvas(width, height) {
				return new Promise((resolve, reject) => {
					// Ëé∑ÂèñÁîªÂ∏É‰∏ä‰∏ãÊñá
					const ctx = uni.createCanvasContext('certificateCanvas', this);
					const scale = 3; // Áº©ÊîæÊØî‰æãÔºåÊèêÈ´òÊ∏ÖÊô∞Â∫¶
					const w = width * scale;
					const h = height * scale;

					// 1. ÁªòÂà∂ËØÅ‰π¶ËÉåÊôØ
					ctx.setFillStyle('#FFFBF2');
					ctx.fillRect(0, 0, w, h);
					
					// 2. ÁªòÂà∂ËØÅ‰π¶ËæπÊ°Ü
					ctx.setStrokeStyle('#D4A574');
					ctx.setLineWidth(18);
					ctx.roundRect(10, 10, w - 20, h - 20, 96);
					ctx.stroke();
					
					// 3. ÁªòÂà∂ËØÅ‰π¶Ê†áÈ¢ò
					ctx.setFontSize(168);
					ctx.setFillStyle('#4D0000');
					ctx.setTextAlign('center');
					ctx.fillText('ÂäüÂæ∑ËØÅ‰π¶', w / 2, 240);
					
					ctx.setFontSize(66);
					ctx.setFillStyle('#999');
					ctx.fillText('Certificate of Merit', w / 2, 320);
					
					// 4. ÁªòÂà∂ÂàÜÈöîÁ∫ø
					ctx.setStrokeStyle('#D4A574');
					ctx.setLineWidth(6);
					ctx.beginPath();
					ctx.moveTo(w / 2 - 600, 380);
					ctx.lineTo(w / 2 + 600, 380);
					ctx.stroke();
					
					// 5. ÁªòÂà∂ÂäüÂæ∑‰∏ª‰ø°ÊÅØ
					ctx.setFontSize(72);
					ctx.setFillStyle('#999');
					ctx.fillText('ÂäüÂæ∑‰∏ª', w / 2, 500);
					
					ctx.setFontSize(144);
					ctx.setFillStyle('#D4A574');
					ctx.fillText(this.userInfo.nickname || 'ÂñÑ‰ø°', w / 2, 680);
					
					// 6. ÁªòÂà∂ÂäüÂæ∑ÊñáÊ°à
					ctx.setFontSize(108);
					ctx.setFillStyle('#4D0000');
					ctx.fillText('ËôîËØö‰æõÂ•âÔºåÂäüÂæ∑Êó†Èáè', w / 2, 840);
					
					ctx.setFontSize(78);
					ctx.setFillStyle('#999');
					ctx.fillText('ÂøÉËØöÂàôÁÅµÔºåÂñÑÂøµÊàêÁúü', w / 2, 940);
					
					// 7. ÁªòÂà∂‰ø°ÊÅØÂå∫Âüü
					ctx.setFontSize(72);
					ctx.setFillStyle('#999');
					ctx.fillText('Á¥ØËÆ°È¶ôÁÅ´ÂÄº', w / 2 - 400, 1100);
					ctx.fillText('Ëé∑ÂæóÊó•Êúü', w / 2 + 400, 1100);
					
					ctx.setFontSize(84);
					ctx.setFillStyle('#D4A574');
					ctx.fillText(`${this.totalIncense || this.userInfo.balance} ÁÇπ`, w / 2 - 400, 1220);
					ctx.fillText(this.certificateDate, w / 2 + 400, 1220);
					
					// 8. ÁªòÂà∂Âç∞Á´†
					ctx.setStrokeStyle('#D4A574');
					ctx.setLineWidth(12);
					ctx.arc(w - 420, 1400, 420, 0, 2 * Math.PI);
					ctx.stroke();
					
					ctx.setFontSize(78);
					ctx.setFillStyle('#D4A574');
					ctx.fillText('ÂäüÂæ∑', w - 420, 1360);
					ctx.fillText('ÂúÜÊª°', w - 420, 1460);
					
					// 9. ÁªòÂà∂Â∫ïÈÉ®‰ø°ÊÅØ
					ctx.setStrokeStyle('#D4A574');
					ctx.setLineWidth(6);
					ctx.beginPath();
					ctx.moveTo(w / 2 - 600, 1700);
					ctx.lineTo(w / 2 + 600, 1700);
					ctx.stroke();
					
					ctx.setFontSize(96);
					ctx.setFillStyle('#4D0000');
					ctx.fillText('Â¶àÁ•ñÊñáÂåñ‰º†Êâø', w / 2, 1820);
					
					ctx.setFontSize(60);
					ctx.setFillStyle('#999');
					ctx.fillText('Mazu Cultural Heritage', w / 2, 1900);
					
					// ÁªòÂà∂ÂÆåÊàêÔºåÁîüÊàêÂõæÁâá
					ctx.draw(false, () => {
						uni.canvasToTempFilePath({
							x: 0,
							y: 0,
							width: w,
							height: h,
							destWidth: w,
							destHeight: h,
							canvasId: 'certificateCanvas',
							success: (res) => {
								resolve(res.tempFilePath);
							},
							fail: (err) => {
								reject(err);
							}
						}, this);
					});
				});
			},
			/**
			 * ÂàÜ‰∫´ËØÅ‰π¶ÔºàÊîØÊåÅÂ•ΩÂèã/ÊúãÂèãÂúàÔºâ
			 */
			async shareCertificate() {
				try {
					// 1. ÁîüÊàêËØÅ‰π¶ÂõæÁâá
					const imagePath = await this.convertCertificateToImage();
					
					// 2. ÊòæÁ§∫ÂàÜ‰∫´ÈÄâÈ°π
					uni.showActionSheet({
						itemList: ['ÂàÜ‰∫´ÁªôÂæÆ‰ø°Â•ΩÂèã', 'ÂàÜ‰∫´Âà∞ÊúãÂèãÂúà'],
						success: (res) => {
							if (res.tapIndex === 0) {
								// ÂàÜ‰∫´ÁªôÂ•ΩÂèã
								uni.shareAppMessage({
									title: `ÊàëÁöÑÂ¶àÁ•ñÂäüÂæ∑ËØÅ‰π¶`,
									path: '/pages/certificate/certificate',
									imageUrl: imagePath,
									success: () => {
										uni.showToast({
											title: 'ÂàÜ‰∫´ÊàêÂäü',
											icon: 'success'
										});
									},
									fail: (err) => {
										console.error('ÂàÜ‰∫´Â•ΩÂèãÂ§±Ë¥•Ôºö', err);
										uni.showToast({
											title: 'ÂàÜ‰∫´Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
											icon: 'none'
										});
									}
								});
							} else if (res.tapIndex === 1) {
								// ÂàÜ‰∫´Âà∞ÊúãÂèãÂúà
								uni.shareTimeline({
									title: `ÊàëÁöÑÂ¶àÁ•ñÂäüÂæ∑ËØÅ‰π¶ÔºåÁ¥ØËÆ°ÊçêÁåÆ${this.userInfo.balance || 0}ÁÇπÈ¶ôÁÅ´ÂÄºÔºÅ`,
									imageUrl: imagePath,
									success: () => {
										uni.showToast({
											title: 'ÂàÜ‰∫´ÊàêÂäü',
											icon: 'success'
										});
									},
									fail: (err) => {
										console.error('ÂàÜ‰∫´ÊúãÂèãÂúàÂ§±Ë¥•Ôºö', err);
										uni.showToast({
											title: 'ÂàÜ‰∫´Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
											icon: 'none'
										});
									}
								});
							}
						},
						fail: (err) => {
							console.error('ÂèñÊ∂àÂàÜ‰∫´Ôºö', err);
						}
					});
				} catch (err) {
					console.error('ÂàÜ‰∫´ËØÅ‰π¶Â§±Ë¥•Ôºö', err);
					uni.showToast({
						title: 'ÂàÜ‰∫´Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
						icon: 'none'
					});
				}
			},
			/**
			 * ‰øùÂ≠òËØÅ‰π¶ÂõæÁâáÂà∞Áõ∏ÂÜå
			 */
			async saveCertificate() {
				try {
					// 1. ÁîüÊàêËØÅ‰π¶ÂõæÁâá
					const imagePath = await this.convertCertificateToImage();
					
					// 2. Ê£ÄÊü•Áõ∏ÂÜåÊùÉÈôê
					const settingRes = await uni.getSetting();
					if (!settingRes.authSetting['scope.writePhotosAlbum']) {
						// Áî≥ËØ∑ÊùÉÈôê
						try {
							await uni.authorize({
								scope: 'scope.writePhotosAlbum'
							});
						} catch (authErr) {
							// Áî®Êà∑ÊãíÁªùÊéàÊùÉÔºåÂºïÂØºÂà∞ËÆæÁΩÆÈ°µ
							uni.showModal({
								title: 'ÊèêÁ§∫',
								content: 'ÈúÄË¶ÅÊÇ®ÊéàÊùÉ‰øùÂ≠òÂõæÁâáÂà∞Áõ∏ÂÜåÁöÑÊùÉÈôê',
								confirmText: 'ÂéªËÆæÁΩÆ',
								success: (modalRes) => {
